import React, { useEffect, useState } from 'react';
import { sendMessage } from './api/messages';
import './App.css';
import { useReader } from './useReader';
import RecordBtn from './RecordBtn/RecordBtn';
import { SttSettings, useStt } from './useStt';
import { useLogger } from '@project/ui_lib';
import VoiceAssistantSphere, { VoiceAssistantStatus } from './Blob/Blob';

function App() {
  const [chatAnswer, setChatAnswer] = useState('');
  const [assistantStatus, setAssistantStatus] = useState<VoiceAssistantStatus>('waiting');
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (isLoading) {
      setAssistantStatus('loading');
    }
  }, [isLoading]);

  const sendHandler = (message: string) => {
    (async () => {
      if (!message) {
        alert('Nothing to send');
        return;
      }
      console.log('Request stated');
      setIsLoading(true);
      const resp = await sendMessage([{ role: 'user', content: message }]);

      setIsLoading(false);
      console.log('Request finished');
      setChatAnswer(resp.choices[0].message.content);
    })();
  }

  const sttSettings: SttSettings = {
    onSpeechFinished: sendHandler
  };

  const {
    fullTranscription,
    tmpTranscription,
    speechStatus,
    speechIsAvailable,
    startSpeechHandler,
    stopSpeechHandler,
    resetTmpTranscription,
    resetFullTranscription
  } = useStt(sttSettings);

  const assistantSphereOnClick = () => {
    if (speechStatus === 'active') {
      stopSpeechHandler();
    } else {
      startSpeechHandler();
    }
  }

  useEffect(() => {
    let newAssistantStatus:VoiceAssistantStatus = 'waiting';

    switch (speechStatus) {
      case 'error':
        newAssistantStatus = 'error';
        break;
      case 'active':
        newAssistantStatus = 'listening';
        break;
      case 'stopped':
        newAssistantStatus = 'waiting';
    }

    if (assistantStatus !== 'loading') {
      setAssistantStatus(newAssistantStatus);
    }
  }, [speechStatus]);

  useReader({
    text: chatAnswer,
    onReadStart: () => setAssistantStatus('voicing'),
    onReadFinished: () => setAssistantStatus('waiting')
  });
  
  return (
    <div className="App">
      <div>
        <VoiceAssistantSphere
          onClick={assistantSphereOnClick}
          status={assistantStatus}
        />
        {/*<button onClick={startSpeechHandler} >Start</button>*/}
        {/*<button onClick={stopSpeechHandler} >Stop</button>*/}
        {/*<span>Status: { speechStatus }</span>*/}
        {/*<div>*/}
        {/*  <span>Full transcription</span>*/}
        {/*  <input type="text" name="" id="" readOnly={true} value={fullTranscription} />*/}
        {/*</div>*/}
        {/*<div>*/}
        {/*  <span>Tmp transcription</span>*/}
        {/*  <input type="text" name="" id="" readOnly={true} value={tmpTranscription} />*/}
        {/*</div>*/}
      </div>
    </div>
  );
}

export default App;
